import uniIcon from '../uni-icon/uni-icon.vue'
import uniBadge from '../uni-badge/uni-badge.vue'
export default {
  name: 'UniListItem',
  components: {
    uniIcon,
    uniBadge
  },
  props: {
    title: {
      type: String,
      default: ''
    }, // 列表标题
    note: {
      type: String,
      default: ''
    }, // 列表描述
    disabled: { // 是否禁用
      type: Boolean,
      default: false
    },
    showArrow: { // 是否显示箭头
      type: Boolean,
      default: true
    },
    showBadge: { // 是否显示数字角标
      type: Boolean,
      default: false
    },
    showSwitch: { // 是否显示Switch
      type: Boolean,
      default: false
    },
    switchChecked: { // Switch是否被选中
      type: Boolean,
      default: false
    },
    badgeText: {
      type: [String, Number],
      default: ''
    }, // badge内容
    badgeType: { // badge类型
      type: String,
      default: 'success'
    },
    thumb: {
      type: String,
      default: ''
    }, // 缩略图
    showExtraIcon: { // 是否显示扩展图标
      type: Boolean,
      default: false
    },
    extraIcon: {
      type: Object,
      default () {
        return {
          type: 'contact',
          color: '#000000',
          size: 20
        }
      }
    }
  },
  data () {
    return {

    }
  },
  methods: {
    onClick () {
      this.$emit('click')
    },
    onSwitchChange (e) {
      this.$emit('switchChange', e.detail)
    }
  }
}
